.ball{
    position: absolute;
    border-radius: 50%;
    animation: shrink 1.2s linear 1;
    transform: scale(0);
    opacity: 0.5;
}

.mouse{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    padding: 0 20px;
    width: 100%;
    color: white;
    font-size: 40px;
    text-transform: uppercase;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 700;
    letter-spacing: 0.8px;
    z-index: -1;
}

@keyframes shrink {
    0%{
        transform: scale(1) ;
    }
    20%{
        transform: scale(1);
    }
    100%{
        transform: scale(0);
    }
}
